          ic: require("@/assets/images/ic-accessary-20.svg"),
          seq: "002",
        },
        {
          title: "고장/수리/오작동",
          ic: require("@/assets/images/ic-brokedown-20.svg"),
          seq: "003",
        },
        {
          title: "앱 사용",
          ic: require("@/assets/images/ic-app-20.svg"),
          seq: "004",
        },
        {
          title: "계정 및 회원 정보",
          ic: require("@/assets/images/ic-my-20.svg"),
          seq: "005",
        },
        {
          title: "구매/반품/환불",
          ic: require("@/assets/images/ic-box-20.svg"),
          seq: "006",
        },
        {
          title: "배송",
          ic: require("@/assets/images/ic-delivery-20.svg"),
          seq: "007",
        },
        {
          title: "기타",
          ic: require("@/assets/images/ic-etc-20.svg"),
          seq: "008",
        },
      ],
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  destroyed() {},
  methods: {
    goto(refName) {
      var element = this.$refs[refName][0];
      var top = element.offsetTop;
      // document.querySelector(".lay-body").scrollTo(0, top - this.barHeight + 36);
      document.querySelector(".lay-body").scrollTo({
        top: top - this.barHeight + 36,
        left: 0,
        behavior: "smooth",
      });
    },
    handelScroll(e) {
      var scrollTp = e.target.scrollTop;
      this.tags.some((item, index) => {
        if (
          scrollTp + this.barHeight + this.sBarHeight - 36 >=
          this.$refs[this.tags[this.tags.length - 1 - index].seq][0].offsetTop
        ) {
          this.selectTag = this.tags.length - 1 - index;
          return true;
        }
      });
    },
  },
};
</script>

